.app-container {
  background: url('background.jpg') no-repeat center center fixed;
  background-size: cover;
  min-height: 100vh;
}

.nav {
  display: flex;
  justify-content: center;
  gap: 20px;
  padding: 20px;
  background: rgba(0, 0, 0, 0.8);
}

.nav-link {
  color: rgb(66, 118, 182);
  text-decoration: none;
  font-size: 20px;
  font-weight: bold;
  text-shadow: 0 0 10px rgb(66, 118, 182);
}

.nav-link:hover {
  color: rgb(240, 224, 212);
  text-shadow: 0 0 15px rgb(240, 224, 212);
}

.bonanza-container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px;
  font-family: 'Arial Black', sans-serif;
  color: #fff;
  position: relative;
}

.wins-ticker {
  background: rgba(0, 0, 0, 0.9);
  padding: 10px;
  text-align: center;
  font-size: 18px;
  color: rgb(240, 224, 212);
  text-shadow: 0 0 10px rgb(240, 224, 212);
  margin-bottom: 20px;
  border: 2px dashed rgb(66, 118, 182);
}

.win-item {
  margin: 0 20px;
  display: inline-block;
  animation: tickerGlow 2s infinite;
}

@keyframes tickerGlow {
  0% { text-shadow: 0 0 10px rgb(240, 224, 212); }
  50% { text-shadow: 0 0 20px rgb(66, 118, 182); }
  100% { text-shadow: 0 0 10px rgb(240, 224, 212); }
}

.title {
  text-align: center;
  color: rgb(66, 118, 182);
  font-size: 48px;
  text-shadow: 0 0 20px rgb(66, 118, 182), 0 0 40px #00ffff;
  animation: neonPulse 1.5s infinite;
}

@keyframes neonPulse {
  0% { text-shadow: 0 0 20px rgb(66, 118, 182), 0 0 40px #00ffff; }
  50% { text-shadow: 0 0 30px rgb(66, 118, 182), 0 0 60px #00ffff; }
  100% { text-shadow: 0 0 20px rgb(66, 118, 182), 0 0 40px #00ffff; }
}

.user-panel {
  text-align: center;
  margin: 20px 0;
  padding: 15px;
  background: rgba(0, 0, 0, 0.7);
  border-radius: 15px;
  font-size: 24px;
  text-transform: uppercase;
  border: 3px solid #00ffff;
  box-shadow: 0 0 20px #00ffff;
}

.cabinet-link {
  color: rgb(240, 224, 212);
  text-decoration: none;
  margin-left: 15px;
  text-shadow: 0 0 10px rgb(240, 224, 212);
}

.cabinet-link:hover {
  color: rgb(66, 118, 182);
  text-shadow: 0 0 15px rgb(66, 118, 182);
}

.slots-container {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin: 40px 0;
}

.slot-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.slot {
  width: 100px;
  height: 100px;
  background: linear-gradient(45deg, rgb(66, 118, 182), #00ffff);
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 48px;
  font-weight: bold;
  color: #000;
  border: 5px solid rgb(240, 224, 212);
  box-shadow: 0 0 25px rgb(240, 224, 212);
  position: relative;
  overflow: hidden;
  perspective: 1000px;
}

.slot.spinning {
  animation: spinDrum 0.2s linear infinite;
}

@keyframes spinDrum {
  0% { transform: rotateX(0deg); }
  100% { transform: rotateX(360deg); }
}

.slot-label {
  margin-top: 10px;
  font-size: 16px;
  color: rgb(66, 118, 182);
  text-shadow: 0 0 10px rgb(66, 118, 182);
}

.sum-display {
  text-align: center;
  margin: 30px 0;
  font-size: 28px;
  text-transform: uppercase;
}

.rgb(66, 118, 182) { color: #ff3366; text-shadow: 0 0 15px #ff3366; }
.yellow { color: #ffff66; text-shadow: 0 0 15px #ffff66; }
.cyan { color: #66ccff; text-shadow: 0 0 15px #66ccff; }
.green { color: #00ff9d; text-shadow: 0 0 15px #00ff9d; }

.controls {
  display: flex;
  justify-content: center;
  gap: 30px;
  align-items: center;
  margin: 30px 0;
}

.bet-section {
  display: flex;
  align-items: center;
  gap: 15px;
}

.bet-input {
  padding: 12px;
  border-radius: 10px;
  border: 3px solid rgb(66, 118, 182);
  width: 100px;
  background: #000;
  color: rgb(240, 224, 212);
  font-size: 18px;
  text-align: center;
}

.spin-button {
  padding: 15px 30px;
  background: linear-gradient(45deg, rgb(66, 118, 182), #00ffff);
  border: 3px solid rgb(240, 224, 212);
  border-radius: 15px;
  color: #000;
  font-size: 24px;
  font-weight: bold;
  cursor: pointer;
  text-transform: uppercase;
  box-shadow: 0 0 20px rgb(240, 224, 212);
  transition: all 0.3s;
}

.spin-button:hover:not(:disabled) {
  transform: scale(1.1);
  box-shadow: 0 0 30px rgb(66, 118, 182);
}

.spin-button:disabled {
  background: #666;
  border-color: #333;
  cursor: not-allowed;
  box-shadow: none;
}

.result {
  text-align: center;
  margin-top: 30px;
  padding: 20px;
  background: rgba(0, 0, 0, 0.7);
  border-radius: 15px;
  font-size: 24px;
  border: 3px solid #00ffff;
}

.win {
  color: #00ff9d;
  text-shadow: 0 0 15px #00ff9d;
}

.lose {
  color: #ff3366;
  text-shadow: 0 0 15px #ff3366;
}

.ad-banner {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 200px;
  padding: 20px;
  background: linear-gradient(45deg, rgb(66, 118, 182), rgb(240, 224, 212));
  color: #000;
  font-size: 18px;
  font-weight: bold;
  text-align: center;
  border-radius: 15px;
  border: 3px solid #00ffff;
  box-shadow: 0 0 25px #00ffff;
  text-transform: uppercase;
}

.left-ad {
  left: -250px;
}

.right-ad {
  right: -250px;
}